<template lang="html">
    <Panel title="" :class="$style.panel">
        <ul :class="$style.content">
            <li>
                <img src="//img12.360buyimg.com/jrpmobile/jfs/t2842/350/3035567089/14791/5f6ff93d/577cf395N31e76288.png?width=1125&height=252" alt="">
            </li>
            <li>
                <div v-for="item in items" :key="item.src">
                    <router-link :to="{name: item.href}">
                        <img :src="item.src" alt="item.title">
                        <p>{{ item.title }}</p>
                    </router-link>
                </div>
            </li>
            <li>Copyright  2004-2018 JD.com </li>
            <li></li>
            <li></li>
            <li></li>
        </ul>
    </Panel>
</template>

<script>
  import Panel from '../core/panel.vue'
  export default {
    components: {
      Panel
    },
    data() {
      return {
        items: [{
          href: 'home',
          src: '//img12.360buyimg.com/jrpmobile/jfs/t2971/333/1297567079/898/f2d2e00d/577dc28dNe5138337.png?width=108&height=108',
          title: ''
        },{
          href: 'home',
          src: '//img12.360buyimg.com/jrpmobile/jfs/t2824/256/2966087355/831/188bfa25/577cf3dcN18aadbf2.png?width=108&height=108',
          title: ''
        },{
          href: 'home',
          src: '//img12.360buyimg.com/jrpmobile/jfs/t2920/282/1283157010/1040/23f1430b/577cf3e5N53f740b8.png?width=108&height=108',
          title: ''
        }]
      }
    }
  }
</script>

<style lang="scss" module>
    @import "../../css/element.scss";
    .panel {
        margin-bottom: 100px !important;
        @include panel;
        &>h4 {
            display: none;
        }
        .content {
            background: #f5f5f5;
            @include flex;
            li {
                width: 100%;
                text-align: center;
                color: #999;
                font-size: 24px;
                height: 143px;
                >img {
                    width: 100%;
                    height: 143px;
                }
                &:nth-child(2) {
                    @include flex(row);
                    height: 186px;
                    box-sizing: border-box;
                    >div {
                        width: 33.3333%;
                        height: 100%;
                        padding-top: 40px;
                        box-sizing: border-box;
                        a {
                            display: block;
                            text-decoration: none;
                            width: 100%;
                            height: 100%;
                            img {
                                display: block;
                                width: 90px;
                                height: 90px;
                                margin: 0 auto;
                            }
                            p {
                                font-size: 26px;
                                text-align: center;
                                color: #999;
                                margin-top: 12px;
                                white-space: nowrap;
                                text-overflow: ellipsis;
                                overflow: hidden;
                            }
                        }
                    }
                }
                &:nth-child(n+3) {
                    padding: 24px 0;
                    height: 38px;
                    line-height: 38px;
                    border-top: 1px solid #ddd;
                }
            }
        }
    }
</style>


